import React from 'react';

const Security = () => {
  return (
    <div className="container mt-5">
      <h2 className="text-center mb-4">Comparao de Segurana e Conformidade em Servios de Nuvem</h2>
      <div className="row">
        <div className="col-md-4">
          <div className="card">
            <div className="card-body">
              <h5 className="card-title">AWS</h5>
              <ul className="list-group list-group-flush">
                <li className="list-group-item">Certificaes de segurana, como ISO 27001 e SOC 2</li>
                <li className="list-group-item">Firewalls e controles de acesso robustos</li>
                <li className="list-group-item">Auditorias de segurana e conformidade regulares</li>
              </ul>
            </div>
          </div>
        </div>
        <div className="col-md-4">
          <div className="card">
            <div className="card-body">
              <h5 className="card-title">Azure</h5>
              <ul className="list-group list-group-flush">
                <li className="list-group-item">Conformidade com padres globais, como GDPR e HIPAA</li>
                <li className="list-group-item">Ferramentas avanadas de criptografia e monitoramento</li>
                <li className="list-group-item">Segurana multicamada em infraestrutura e aplicativos</li>
              </ul>
            </div>
          </div>
        </div>
        <div className="col-md-4">
          <div className="card">
            <div className="card-body">
              <h5 className="card-title">Google Cloud</h5>
              <ul className="list-group list-group-flush">
                <li className="list-group-item">Controles de segurana centralizados e gerenciamento de identidade</li>
                <li className="list-group-item">Proteo contra ameaas avanadas e ataques DDoS</li>
                <li className="list-group-item">Ferramentas de conformidade automtica e relatrios</li>
              </ul>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Security;
