import React from 'react';
import nuvem from '../../images/nuvem.jpg';
import 'bootstrap/dist/css/bootstrap.min.css';

const Banner = () => {
  return (
    <div id="Sobre" className="container my-5">
      <div className="row p-4 pb-0 pe-lg-0 pt-lg-5 align-items-center rounded-3 border shadow-lg">
        <div className="col-lg-7 p-3 p-lg-5 pt-lg-3">
          <h1 className="display-4 fw-bold lh-1 text-body-emphasis fs-2">A Revoluo da Computao em Nuvem: Transforme Seu Negcio com AWS, Google Cloud e Azure</h1>
          <p className="lead">A computao em nuvem est transformando o mundo dos negcios, oferecendo flexibilidade, escalabilidade e inovao sem precedentes. Com a cloud, voc pode acessar recursos poderosos sob demanda, reduzir custos operacionais e acelerar o desenvolvimento de novas solues. Descubra como AWS, Google Cloud e Azure esto liderando essa revoluo tecnolgica e como voc pode aproveitar essas oportunidades.</p>
        </div>
        <div className="col-lg-5 p-0 overflow-hidden shadow-lg">
          <img className="img-fluid rounded-lg-3" src={nuvem} alt="Computao em Nuvem" />
        </div>
      </div>
    </div>
  );
};

export default Banner;
